<template>
  <div class="flex h-screen bg-gray-50 overflow-hidden relative font-sans text-gray-900">
    
    <div v-if="isMobileSidebarOpen" @click="isMobileSidebarOpen = false" 
         class="fixed inset-0 bg-black/60 z-40 lg:hidden backdrop-blur-sm transition-opacity">
    </div>

    <div class="fixed lg:static inset-y-0 left-0 z-50 bg-white shadow-2xl lg:shadow-none transform transition-all duration-300 ease-in-out h-full border-r border-gray-200"
         :class="[isMobileSidebarOpen ? 'translate-x-0' : '-translate-x-full lg:translate-x-0', 'w-64']">
         
         <SellerSidebar 
            :isCollapsed="false"
            :newOrderCount="pendingOrdersCount"
            @close-mobile="isMobileSidebarOpen = false"
            @logout="handleLogout"
         />
    </div>

    <div class="flex-1 flex flex-col min-w-0 h-full w-full relative">
      
      <header class="bg-white border-b border-gray-200 h-16 flex items-center justify-between px-6 sticky top-0 z-30 shadow-sm">
        <div class="flex items-center gap-3">
           <button @click="isMobileSidebarOpen = true" class="lg:hidden text-xl p-2 hover:bg-gray-100 rounded-md">‚ò∞</button>
           <h1 class="font-bold text-gray-800 text-xl tracking-tight">{{ pageTitle }}</h1>
        </div>
        <div class="flex items-center gap-4">
            <div class="hidden md:flex flex-col items-end">
                <span class="text-xs font-bold text-gray-400 uppercase tracking-wider">Store</span>
                <span class="text-sm font-bold text-gray-800">{{ session.sellerCompany || 'Loading...' }}</span>
            </div>
            <div class="h-10 w-10 bg-orange-100 rounded-full flex items-center justify-center text-orange-600 font-bold border-2 border-white shadow-sm">
                {{ getUserInitials() }}
            </div>
        </div>
      </header>

      <div class="flex-1 overflow-y-auto p-4 sm:p-8 custom-scrollbar bg-gray-50/50">
        
        <div v-if="route.name === 'SellerDashboard'" class="grid grid-cols-1 md:grid-cols-3 gap-6 animate-fade-in">
           <div class="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm hover:shadow-md transition-shadow relative overflow-hidden">
              <div class="absolute top-0 right-0 p-4 opacity-10 text-6xl">‚è≥</div>
              <h3 class="text-gray-400 text-xs font-bold uppercase tracking-wider">Pending Orders</h3>
              <p class="text-4xl font-black text-gray-900 mt-2">{{ pendingOrdersCount }}</p>
              <p class="text-xs text-orange-500 font-medium mt-2">Action needed</p>
           </div>
           
           <div class="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm hover:shadow-md transition-shadow relative overflow-hidden">
              <div class="absolute top-0 right-0 p-4 opacity-10 text-6xl">üí∞</div>
              <h3 class="text-gray-400 text-xs font-bold uppercase tracking-wider">Today's Revenue</h3>
              <p class="text-4xl font-black text-gray-900 mt-2">‚Çπ{{ todaySales }}</p>
              <p class="text-xs text-green-500 font-medium mt-2">Gross Sales</p>
           </div>

           <div class="bg-gradient-to-br from-gray-900 to-black p-6 rounded-2xl text-white shadow-lg relative overflow-hidden group cursor-pointer" @click="router.push({name: 'SellerProducts'})">
               <h3 class="text-gray-400 text-xs font-bold uppercase tracking-wider">Quick Action</h3>
               <p class="text-xl font-bold mt-2">Manage Products</p>
               <div class="mt-4 flex items-center gap-2 text-sm font-medium text-gray-300 group-hover:text-white transition-colors">
                   <span>Go to Inventory</span> <span>‚Üí</span>
               </div>
           </div>
        </div>

        <div v-else-if="route.name === 'SellerOrders'" class="max-w-6xl mx-auto space-y-6 animate-fade-in">
           <div v-if="sellerOrders.loading" class="space-y-4">
              <div v-for="n in 3" :key="n" class="h-40 bg-white rounded-xl animate-pulse"></div>
           </div>
           
           <div v-else-if="!sellerOrders.data || !sellerOrders.data.length" class="text-center py-20 bg-white rounded-2xl border border-dashed border-gray-300">
               <div class="text-6xl mb-4 grayscale opacity-20">üì¶</div>
               <p class="text-gray-400 font-bold">No orders received yet.</p>
           </div>
           
           <div v-else v-for="order in sellerOrders.data" :key="order.name" 
                class="bg-white rounded-2xl border border-gray-200 overflow-hidden shadow-sm hover:shadow-md transition-all">
                
                <div class="bg-gray-50/80 px-6 py-4 border-b border-gray-100 flex flex-wrap justify-between items-center gap-4">
                    <div>
                      <div class="flex items-center gap-3">
                          <span class="font-black text-lg text-gray-800">#{{ order.name }}</span>
                          <span class="text-xs text-gray-400 font-mono">{{ order.transaction_date }}</span>
                      </div>
                      <p class="text-sm text-gray-500 mt-1 font-medium">üë§ {{ order.customer_name }}</p>
                    </div>
                    <div class="flex items-center gap-4">
                      <div class="text-right">
                          <span class="block text-[10px] font-bold text-gray-400 uppercase">Total Amount</span>
                          <span class="font-black text-lg">‚Çπ{{ order.grand_total }}</span>
                      </div>
                      <span :class="getStatusClass(order.status)" class="px-3 py-1 rounded-full text-[11px] font-bold uppercase border tracking-wide">
                        {{ order.status }}
                      </span>
                    </div>
                </div>

                <div class="p-6">
                   <div class="space-y-3">
                       <div v-for="item in order.items" :key="item.item_code" class="flex justify-between items-center py-2 border-b border-dashed border-gray-100 last:border-0 last:pb-0">
                          <div class="flex items-center gap-3">
                              <span class="bg-gray-100 text-gray-600 font-bold text-xs px-2 py-1 rounded h-fit">{{ item.qty }}x</span>
                              <span class="text-sm font-medium text-gray-700">{{ item.item_name }}</span>
                          </div>
                          <span class="font-bold text-sm text-gray-900">‚Çπ{{ item.amount }}</span>
                       </div>
                   </div>
                </div>

                <div v-if="['Pending', 'Draft', 'Processing'].includes(order.status)" class="px-6 py-4 bg-gray-50 border-t border-gray-100 flex justify-end gap-3">
                   <button v-if="['Pending', 'Draft'].includes(order.status)" @click="updateStatus(order.name, 'Processing')" 
                           class="bg-blue-600 hover:bg-blue-700 text-white px-5 py-2 rounded-lg text-xs font-bold uppercase shadow-sm transition-transform active:scale-95 flex items-center gap-2">
                           <span>‚úî</span> Accept Order
                   </button>
                   <button v-if="order.status === 'Processing'" @click="updateStatus(order.name, 'Completed')" 
                           class="bg-green-600 hover:bg-green-700 text-white px-5 py-2 rounded-lg text-xs font-bold uppercase shadow-sm transition-transform active:scale-95 flex items-center gap-2">
                           <span>üöö</span> Mark Delivered
                   </button>
                </div>
           </div>
        </div>

        <div v-else-if="route.name === 'SellerProducts'" class="flex flex-col items-center justify-center h-[60vh]">
           <div class="w-20 h-20 bg-gray-100 rounded-full flex items-center justify-center text-4xl mb-4">üöß</div>
           <h2 class="text-xl font-bold text-gray-800">Inventory Management</h2>
           <p class="text-gray-500 mt-2">This module is under construction.</p>
        </div>

      </div>
    </div>
    
    <Toast ref="toastRef" />
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { createResource } from 'frappe-ui'
import { session } from '../data/session'
import SellerSidebar from '../components/SellerSidebar.vue'
import Toast from '../components/Toast.vue'

const route = useRoute()
const router = useRouter()
const isMobileSidebarOpen = ref(false)
const toastRef = ref(null)

const getUserInitials = () => session.user ? session.user.substring(0, 2).toUpperCase() : 'SE'

const pageTitle = computed(() => {
  if (route.name === 'SellerOrders') return 'Order Management'
  if (route.name === 'SellerProducts') return 'Inventory'
  return 'Dashboard Overview'
})

const sellerOrders = createResource({
  url: 'doppio_demo.api.get_seller_orders', 
  auto: true
})

const pendingOrdersCount = computed(() => {
  if (!sellerOrders.data) return 0
  return sellerOrders.data.filter(o => ['Pending', 'Draft'].includes(o.status)).length
})

const todaySales = computed(() => {
    if (!sellerOrders.data) return 0
    const today = new Date().toISOString().split('T')[0]
    return sellerOrders.data
        .filter(o => o.transaction_date === today && o.status !== 'Cancelled')
        .reduce((sum, order) => sum + order.grand_total, 0)
})

const updateStatus = async (orderId, newStatus) => {
    try {
        const res = await fetch('/api/method/doppio_demo.api.update_order_status', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ order_name: orderId, status: newStatus })
        })
        const data = await res.json()
        if (data.message && data.message.status === 'success') {
            toastRef.value.add(`Order status updated to ${newStatus}`, 'success')
            sellerOrders.fetch()
        } else {
            toastRef.value.add('Error: ' + (data.message?.message || 'Failed'), 'error')
        }
    } catch(e) {
        toastRef.value.add('Network Error', 'error')
    }
}

const getStatusClass = (status) => {
    if (['Pending', 'Draft'].includes(status)) return 'bg-yellow-50 text-yellow-700 border-yellow-200 ring-1 ring-yellow-100'
    if (status === 'Completed') return 'bg-green-50 text-green-700 border-green-200 ring-1 ring-green-100'
    if (status === 'Processing') return 'bg-blue-50 text-blue-700 border-blue-200 ring-1 ring-blue-100'
    return 'bg-gray-50 text-gray-600 border-gray-200'
}

const handleLogout = async () => {
    try { 
        await fetch('/api/method/logout', { method: 'POST' })
        session.isLoggedIn = false
        session.role = null
        window.location.href = '/login'
    } catch (e) { window.location.href = '/login' }
}
</script>

<style scoped>
.animate-fade-in { animation: fadeIn 0.4s ease-out; }
@keyframes fadeIn { from { opacity: 0; transform: translateY(10px); } to { opacity: 1; transform: translateY(0); } }
</style>